import React, { Component } from 'react';
import isInt from 'validator/lib/isInt';
import isEmpty from 'validator/lib/isEmpty';

class InputNumber extends Component {

    state = { 
        value: '',
        paymentType:  this.props.paymentType
     }

     handleInputChange = (event)=>{
         this.setState({
             value: event.target.value
         });
     };

     handleOnClickInputBtn = (event)=>{
        event.preventDefault();
        const send = this.state.value;
        const sender = send.trim();
        if(!isEmpty(sender)){
            if(isInt(sender)){
                const t = String(sender);
                const i = t.length;
                if (i===9) {
                    const type = this.state.paymentType;
                    switch (type) {
                        case "paypal":
                            alert("le paiement par "+type+" est temporairement indisponible");
                            break;
                        case "om":
                            alert('le paiement par '+type+' est temporairement indisponible');
                            break;        
                        case "momo":
                            alert(`le numero ${sender} souhait effectuer un depos par ${type}`);
                            break;
                        case "eu":
                            alert('le paiement par '+type+' est temporairement indisponible');
                            break;
                        case "visa":
                            alert('le paiement par '+type+' est temporairement indisponible');  
                            break;        
                        default:
                            break;
                    }                    
                }else{
                    alert('entrez un numero standart de neuf (09) caracteres')
                }
            }else{
                alert('saisissez votre numero')
            }
        }else{
            alert('remplissez le champ');
        }        
    };

    render() {

        const {value} = this.state;

        return (
                <form id="inputForm" className="formGroup container formGroup w-50">
                    <div className="my-2">
                        <input className="formControl w-100"
                        type="number"
                        value={value}
                        onChange={this.handleInputChange}
                        id="number"
                        placeholder="saisissez votre numero"/>
                    </div>
                    <div className="my-2">
                        <input className="btn btn-primary w-100"
                        onClick={this.handleOnClickInputBtn}
                        type="button"
                        id="buttonN"
                        value="Commencer la transaction"/>
                    </div>
                </form>
        );
    }
}
 
export default InputNumber;