import React, { Component } from 'react';
import om from './om.png';
import './om.css';

class Om extends Component {
    state = { 
        src: om,
        alt: "logo orange money"
     }

    render() {
        const {onClickOm}=this.props;
        return (
            <div className="container col w-auto">
                <img
                    id="om"
                    onClick={() => onClickOm()}
                    className="btn"
                    src={this.state.src}
                    alt={this.state.alt}
                />
            </div>
        );
    }
}
 
export default Om;