import Axios from 'axios';
import React, { Component } from 'react';
import './ContactForm.css';
import isEmpty from 'validator/lib/isEmpty';
import isEmail from 'validator/lib/isEmail';
import isInt from 'validator/lib/isInt';

class ContactForm extends Component {
    state = {
        email:'',
        number:'',
        message:''
     }
     handleChangeMail = event =>{
         this.setState({
             email: event.target.value
         });
     }
     handleChangeNumber = event =>{
         this.setState({
             number: event.target.value
         });
     }
     handleChangeMessage = event =>{
         this.setState({
             message: event.target.value
         });
     }
     resetState = ()=>{
         this.setState({
            email:'',
            number:'',
            message:''
         })
     }
     handleSubmit = event => {
         event.preventDefault();
         const emailValue = this.state.email;    
         const numberValue = this.state.number;         
         const messageValue = this.state.message;
         emailValue.trim();
         numberValue.trim();
         messageValue.trim();
         if (!isEmpty(emailValue) && !isEmpty(numberValue) && !isEmpty(messageValue)) {
             if (isEmail(emailValue) && isInt(numberValue)) {
                 const t = String(numberValue);
                 const i = t.length;
                 if(i===9){
                     Axios.post("http://localhost:3001/send-comment", {email:emailValue, number:numberValue, message:messageValue}).then((response)=>{
                        this.resetState()
                     });                    
                 }else{
                    alert("entrez un numero standart de 9 caracteres")
                 }
             }else{
                 alert('entrez un e-mail ou une numero valide')
             }
         }else{
             alert('remplissez tout les champ de contact')
         }
     }
    render() { 
        const {email, number, message} = this.state;
        return ( 
            <form id="contact-form">
                <input type="email" value={email} onChange={this.handleChangeMail} placeholder="votre e-mail"/>
                <input type="number" value={number} onChange={this.handleChangeNumber} placeholder="vetre numero +237"/>
                <textarea type="text" value={message} onChange={this.handleChangeMessage} placeholder="votre message"/>
                <button type="submit" onClick={this.handleSubmit}>Envoyer</button>
            </form>
         );
    }
}
 
export default ContactForm;