import React, { Component } from 'react';
import visa from './visa.png';
import './visa.css';

class Visa extends Component {
    state = { 
        src: visa,
        alt: "logo orange money"
     }

    render() {
        const {onClickVisa}=this.props;
        return (
            <div className="container col w-auto">
                <img
                    id="visa"
                    onClick={() => onClickVisa()}
                    className="btn"
                    src={this.state.src}
                    alt={this.state.alt}
                />
            </div>
        );
    }
}
 
export default Visa;