import React, { Component } from 'react';
import logo from './favico.png';
import admin from './admin.jpg';
import './App.css';
import Om from './components/om/om';
import Momo from './components/momo/momo';
import Eu from './components/eu/eu';
import Visa from './components/visa/visa';
import Paypal from './components/paypal/paypal';
import InputNumber from './components/number/inputNumber';
import ContactForm from './components/contactForm/ContactForm';

class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      visible:false,
      toggle: 0,
      paymentType:''
    }
  }

  onClickOm = ()=>{
    this.setState({
      paymentType:"om",
      visible: this.state.toggle === 1 ? false : true,
      toggle: this.state.visible === true ? 0 : 1,
    });
  }

  onClickEu = ()=>{
    this.setState({
      paymentType:"eu",
      visible: this.state.toggle === 1 ? false : true,
      toggle: this.state.visible === true ? 0 : 1,
    });
  }

  onClickMomo = ()=>{
    this.setState({
      paymentType:"momo",
      visible: this.state.toggle === 1 ? false : true,
      toggle: this.state.visible === true ? 0 : 1,
    });
  }

  onClickVisa = ()=>{
    this.setState({
      paymentType:"visa",
      visible: this.state.toggle === 1 ? false : true,
      toggle: this.state.visible === true ? 0 : 1,
    });
  }

  onClickPaypal = ()=>{
    this.setState({
      paymentType:"paypal",
      visible: this.state.toggle === 1 ? false : true,
      toggle: this.state.visible === true ? 0 : 1,
    });
  }

  render(){
    return (
      <div className="App">
        <header className="App-header container-fluid">
          <img src={logo} className="App-logo" alt="logo" />
        </header> 
        <main className="container">
          <div className="row w-100 px-5">
            <Om onClickOm={this.onClickOm}/>
            <Momo onClickMomo={this.onClickMomo}/>
            <Eu onClickEu={this.onClickEu}/>
            <Visa onClickVisa={this.onClickVisa}/>
            <Paypal onClickPaypal={this.onClickPaypal}/>
          </div>
          <div>
            {this.state.visible == false ? null : <InputNumber paymentType={this.state.paymentType}/>}
          </div>
        </main>
        <footer id="footer" className="container-fluid m-auto col">
          <div id="infos-section" className="row">
            <div className="col-8 infos-subsection" id="infos-subsection1">
              <div className="row">
                <div className="pt-3 col-6 contact pr-2 pl-5">
                  <span id="promoteur">PROMOTEUR</span>
                  <span id="mon">MBOHOU</span>
                  <small id="prenom">Hermann</small>
                  <small id="status">developpeur</small>
                  <span id="email">hermann.mbohou@gmail.com</span>
                  <span id="num">(+237) 697588261</span>
                </div>
                <div className="col-6">
                  <img className="img img-fluid" id="admin-image" src={admin} alt="admin image" />
                </div>
              </div>
            </div>
            <div className="col-4 infos-subsection" id="infos-subsection2">
              <h5>Contactez-nous</h5>
              <ContactForm/>
            </div>
          </div>
          <div id="cr-section" className="row">
           <i className="fa fa-copyright text-muted" aria-hidden="true"></i>
           <small className="text-muted">Un_Mal from M3HEENK-TECH</small>
          </div>
        </footer>
      </div>
    );
  }
}

export default App;
