import React, { Component } from 'react';
import momo from './momo.png';
import './momo.css';

class Momo extends Component {
    state = { 
        src: momo,
        alt: "logo orange money"
     }

    render() {
        const {onClickMomo}=this.props;
        return (
            <div className="container col w-auto">
                <img
                    id="momo"
                    onClick={() => onClickMomo()}
                    className="btn"
                    src={this.state.src}
                    alt={this.state.alt}
                />
            </div>
        );
    }
}
 
export default Momo;