import React, { Component } from 'react';
import paypal from './paypal.png';
import './paypal.css';

class Paypal extends Component {
    state = { 
        src: paypal,
        alt: "logo orange money"
     }

    render() {
        const {onClickPaypal}=this.props;
        return (
            <div className="container col w-auto">
                <img
                    id="paypal"
                    onClick={() => onClickPaypal()}
                    className="btn"
                    src={this.state.src}
                    alt={this.state.alt}
                />
            </div>
        );
    }
}
 
export default Paypal;