import React, { Component } from 'react';
import eu from './eu.png';
import './eu.css';

class Eu extends Component {
    state = { 
        src: eu,
        alt: "logo orange money"
     }

    render() {
        const {onClickEu}=this.props;
        return (
            <div className="container col w-auto">
                <img
                    id="eu"
                    onClick={() => onClickEu()}
                    className="btn"
                    src={this.state.src}
                    alt={this.state.alt}
                />
            </div>
        );
    }
}
 
export default Eu;